<template>
    <el-container>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="IP"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>

                <el-table-column
                    prop="BeginIP"
                    label="开始IP段"
                ></el-table-column>

                <el-table-column prop="EndIP" label="结束IP段">
                </el-table-column>
                <el-table-column prop="updatedate" label="更新时间">
                </el-table-column>
                <el-table-column prop="iptype" label="类型"> </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
                class="file-dialog"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-col :span="12">
                        <el-form-item label="开始IP段" prop="BeginIP">
                            <el-input
                                v-model="editForm.BeginIP"
                                auto-complete="off"
                                maxlength="40"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="结束IP段" prop="EndIP">
                            <el-input
                                v-model="editForm.EndIP"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="类型" prop="iptype">
                            <el-input
                                v-model="editForm.iptype"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div
                    slot="footer"
                    style="margin-top: 10px"
                    class="dialog-footer"
                >
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
                class="file-dialog"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-col :span="12">
                        <el-form-item label="开始IP段" prop="BeginIP">
                            <el-input
                                v-model="addForm.BeginIP"
                                auto-complete="off"
                                maxlength="40"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="结束IP段" prop="EndIP">
                            <el-input
                                v-model="addForm.EndIP"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="类型" prop="iptype">
                            <el-input
                                v-model="addForm.iptype"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-form>
                <div
                    slot="footer"
                    style="margin-top: 10px"
                    class="dialog-footer"
                >
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import { getippagelist, addip, editip, delip } from "../../api/api";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import { mfogtype } from "../../assets/dic/dic";
export default {
    components: { Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                ID: [
                    {
                        required: true,
                        message: "请选择一条记录",
                        trigger: "blur",
                    },
                ],
                BeginIP: [
                    {
                        required: true,
                        message: "请填写开始IP段",
                        trigger: "blur",
                    },
                ],
                EndIP: [
                    {
                        required: true,
                        message: "请填写结束IP段",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {},
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                ID: [
                    {
                        required: true,
                        message: "请选择一条记录",
                        trigger: "blur",
                    },
                ],
                BeginIP: [
                    {
                        required: true,
                        message: "请填写开始IP段",
                        trigger: "blur",
                    },
                ],
                EndIP: [
                    {
                        required: true,
                        message: "请填写结束IP段",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            canEdit: false,
            canDelete: false,
            mfogtype: mfogtype,
        };
    },
    computed: {},
    methods: {
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getippagelist(paras).then((res) => {
                if (res.success) {
                    let tmpdata = res.data.data || [];

                    this.builds = tmpdata;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },

        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { ID: row.ID };
                    delip(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList(this.page);
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });
                return;
            }
            this.editFormVisible = true;

            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
            if (this.$refs["addForm"]) {
                this.$refs["addForm"].clearValidate();
            }
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    // let para = Object.assign({}, this.editForm);
                    let para = JSON.parse(JSON.stringify(this.editForm));

                    editip(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);

                    addip(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                }
            });
        },
    },
    mounted() {
        this.getPageList();

        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>



